.contacts {
  padding-bottom: 55px;
}

.contacts__title {
  width: 100%;
}

.contacts__sub-title {
  font-weight: bold;
  font-size: 16px;
  line-height: 26px;
}

.contacts__wrapper-inner-top {
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: @medium) {
    flex-direction: column;
    margin-bottom: 30px;
  }

  @media screen and (max-width: @medium) {
    box-sizing: border-box;
    padding: 0 10px;
  }
}

.contacts__address-wrap {
  width: 100%;
  max-width: 420px;
  margin-right: 75px;

  @media screen and (max-width: @small) {
    margin-right: 0;
    margin-bottom: 30px;
  }
}

ul.contacts__list-address {
  padding-left: 20px;
  list-style: outside;
}

.contacts__wrapper-inner-bottom {
  @media screen and (max-width: @small) {
    box-sizing: border-box;
    padding: 0 10px;
  }
}

.contacts__tel-wrapper {
  margin-bottom: 30px;
}

.contacts__list-tel-link,
.contacts__list-social-link {
  color: @color-text;

  &:hover,
  &:active {
    color: @color-text;
    text-decoration: none;
    opacity: .8;
  }
}

.contacts__list-social-item {
  padding-left: 40px;
  position: relative;
  margin-bottom: 20px;

  &:first-child::after {
    content: "";
    display: block;
    width: 28px;
    height: 28px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("../img/icons/instagram-dark-icon.png");
  }

  &:last-child::after {
    content: "";
    display: block;
    width: 28px;
    height: 28px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("../img/icons/facebook-dark-icon.png");
  }

  &:last-child {
    margin-bottom: 0;
  }
}
