.form-wrapper {
  background-color: @white;
  max-width: 420px;
  width: 100%;
  min-height: 530px;
  padding: 38px;
  padding-top: 30px;
  border-radius: 25px;
  box-shadow: 9.235px 11.82px 55px 0 rgba(0, 0, 0, 0.1);

  @media screen and (max-width: @extra-large) {
    max-width: 370px;
    min-height: 480px;
    padding: 24px;
  }

  @media screen and (max-width: @small) {
    margin: 0 auto;
  }

  @media all and (max-width: 485px) {
    max-width: 300px;
  }
}

.form-wrapper__title {
  font-size: 24px;
  line-height: 34px;
  font-weight: 800;
  text-align: center;
  margin-bottom: 15px;
  display: block;

  @media screen and (max-width: @large) {
    font-size: 16px;
  }

  @media screen and (max-width: @small) {

  }
}

.form-wrapper__txt {
  color: @light-color-text;
  text-align: center;

  @media screen and (max-width: @large) {
    font-size: 12px;
  }
}

.form-wrapper {
  input {
    width: 100%;
    margin-bottom: 15px;
    border: 1px solid @border-color;
    border-radius: 25px;
    padding-top: 21px;
    padding-bottom: 21px;
    padding-left: 25px;
    transition: all ease 0.3s;

    @media screen and (max-width: @large) {
      padding-top: 16px;
      padding-bottom: 16px;
      padding-left: 21px;
    }
  }

  input:not(.form__submit-btn) {
    cursor: pointer;
  }

  input:not(.form__submit-btn):hover {
    border: 1px solid @blue;
  }

  input:not(.form__submit-btn):focus {
    -webkit-box-shadow: 0 0 10px 0 rgba(36, 85, 153, 0.6);
    -moz-box-shadow: 0 0 10px 0 rgba(36, 85, 153, 0.6);
    box-shadow: 0 0 10px 0 rgba(36, 85, 153, 0.6);
    outline: none;
  }

  input:not(.form__submit-btn):invalid {
    border-color: @red;
    -webkit-box-shadow: 0 0 10px 0 rgba(137, 42, 42, 1);
    -moz-box-shadow: 0 0 10px 0 rgba(137, 42, 42, 1);
    box-shadow: 0 0 10px 0 rgba(137, 42, 42, 1);
    outline: none;
  }

  input[type=submit] {
    color: @white;

    &:hover {
      opacity: 0.9;
    }
  }
}

