.mobile-btn {
  width: 54px;
  height: 54px;
  outline: none;
  background-color: #892a2a;
  border: 1px solid #ffffff;
  border-right: none;
}
.mobile-btn::before {
  content: "";
  display: block;
  width: 44px;
  height: 6px;
  position: absolute;
  top: 8px;
  left: 5px;
  background-color: #ffffff;
  border-radius: 5px;
}
.mobile-btn::after {
  content: "";
  display: block;
  width: 44px;
  height: 6px;
  position: absolute;
  bottom: 8px;
  left: 5px;
  background-color: #ffffff;
  border-radius: 5px;
}
.mobile-btn__decor {
  display: block;
  width: 36px;
  height: 6px;
  position: absolute;
  top: 23px;
  left: 12px;
  background-color: #ffffff;
  border-radius: 5px;
}
.mobile-btn__close::before {
  transform: rotate(45deg);
  top: 22px;
}
.mobile-btn__close::after {
  transform: rotate(-45deg);
  bottom: 24px;
}
.mobile-btn__close .mobile-btn__decor {
  display: none;
}
