.additional-offer {
  border-top: 1px solid @border-color;
  margin-bottom: 65px;
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 433px;
    height: 690px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url('../img/bg-decor-3.png');
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: 433px 690px;
    z-index: -1;
  }

  &::before {
    content: "";
    display: block;
    width: 433px;
    height: 690px;
    position: absolute;
    top: 0;
    right: 0;
    background-image: url('../img/bg-decor-4.png');
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: 433px 690px;
    z-index: -1;
  }

  @media screen and (max-width: 475px) {

    &::after {
      width: 150px;
    }

    &::before {
      width: 150px;
    }
  }

}

.additional-offer__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  z-index: 1;

  @media screen and (max-width: @extra-large) {
    padding: 0 100px;
  }

  @media screen and (max-width: @large) {
    padding: 0 50px;
  }

  @media screen and (max-width: @medium) {
    padding: 0px;
  }
}

.additional-offer__list-item {
  width: 260px;
  height: 260px;
  margin: 5px;
  position: relative;
  border-radius: 10px;
  background-image: url(../img/paint-bg.jpg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50%;
  background-color: @grey;
  margin-left: 15px;
  margin-right: 15px;

  &:first-child {
    margin-left: 0;

    @media screen and (max-width: @extra-large) {
      margin-left: auto;
    }
  }

  &:last-child {
    margin-right: 0;

    @media screen and (max-width: @extra-large) {
      margin-right: auto;
    }
  }

  &:nth-child(2) {
    background-image: url(../img/chess-bg.jpg);
  }

  &:nth-child(3) {
    padding-top: 8px;
    background-image: url(../img/calculator-bg.jpg);
  }

  &:nth-child(4) {
    background-image: url(../img/book-bg.jpg);

    .additional-offer__item-title {
      box-sizing: border-box;
      padding: 0 10px;
    }
  }

  @media screen and (max-width: @extra-large) {
    margin: auto;
    margin-bottom: 30px;
  }
}

.additional-offer__list-link {
  display: block;
  width: 100%;
  height: 100%;
  transition: all ease .3s;
  z-index: 2;
  text-align: center;
  box-sizing: border-box;
  padding-top: 55px;

  &:hover {
    transform: scale(1.1);
    text-decoration: none;
  }
}

.additional-offer__item-title {
  text-decoration: none;
  color: @white;
  font-weight: bold;
  margin-top: 18px;
  display: block;
}

