.menthors {
  padding-top: 10px;
}

.menthors__title {
  padding-bottom: 30px;
}

.menthors__dscr {
  margin-bottom: 55px;

  @media screen and (max-width: @small) {
    box-sizing: border-box;
    padding: 0 15px;
  }
}

.menthors__list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;

  @media screen and (max-width: @laptop) {
    padding: 10px;
    box-sizing: border-box;
  }

  @media screen and (max-width: @small) {
    justify-content: center;
  }
}

.menthors__list-item {
  width: 260px;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 20px;

  &:last-child {
    align-self: flex-start;
  }

  @media screen and (max-width: @large) {
    width: 210px;
  }

  @media screen and (max-width: @small) {
    width: 260px;
    text-align: center;
  }
}

.menthors__list-img {
  height: auto;
  width: 100%;
  object-fit: cover;
  border-radius: 30px;
}

.menthors__list-title {
  display: block;
  font-size: 20px;
  line-height: 28px;
  margin-top: 15px;
  margin-bottom: 15px;
  box-sizing: border-box;
}


