.header {
  background-color: #f5f5f5;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
}

.header__wrapper {
  position: relative;
  box-sizing: border-box;
}

.header__top {
  background-color: @red;
  min-height: 60px;
  box-sizing: border-box;
  padding-left: 247px;
  padding-top: 17px;
  padding-right: 10px;
  position: relative;

  @media screen and (max-width: @extra-large) {
    padding-left: 197px;
  }

  @media screen and (max-width: @large) {
    padding-left: 177px;
  }

  @media screen and (max-width: @small) {
    padding-top: 12px;
    padding-left: 140px;
  }
}

.header__top-btn {
  display: none;

  @media screen and (max-width: @medium) {
    display: block;
    position: absolute;
    bottom: -54px;
    right: 0;
  }
}

.header__top-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  @media screen and (max-width: @laptop) {
    justify-content: flex-start;
  }

  @media screen and (max-width: 475px) {
    padding: 0 0;
  }
}

.header__logo-wrapper {
  border-radius: 50%;
  background-color: rgb(255, 255, 255);
  box-shadow: 9.235px 11.82px 55px 0 rgba(0, 0, 0, 0.08);
  position: absolute;
  width: 247px;
  height: 247px;
  top: -39px;
  left: -39px;
  z-index: 3;
  transition: all ease 0.3s;

  &:hover {
    box-shadow: 9.235px 11.82px 55px 0 rgba(0, 0, 0, 0.18);

    .header__logo-img {
      transform: scale(1.05);
    }
  }

  @media screen and (max-width: @extra-large) {
    width: 207px;
    height: 207px;
  }

  @media screen and (max-width: @large) {
    width: 197px;
    height: 197px;
  }

  @media screen and (max-width: @medium) {
    width: 177px;
    height: 177px;
  }

  @media screen and (max-width: @small) {
    width: 157px;
    height: 157px;
  }
}

.header__logo-link {
  display: block;
  width: 100%;
  height: 100%;
}

.header__logo-img {
  position: absolute;
  top: 49px;
  left: 49px;
  transition: all ease 0.3s;

  @media screen and (max-width: @extra-large) {
    width: 119px;
    height: 119px;
  }

  @media screen and (max-width: @large) {
    width: 112px;
    height: 112px;
  }

  @media screen and (max-width: @medium) {
    width: 107px;
    height: 107px;
  }

  @media screen and (max-width: @small) {
    width: 77px;
    height: 77px;
  }
}

.header__address-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 480px;
  margin-bottom: 17px;

  @media all and (max-width: @laptop) {
    margin-right: 40px;
  }

  @media screen and (max-width: @extra-large) {
    max-width: 420px;
  }

  @media screen and (max-width: @large) {
    max-width: 370px;
    margin-right: 20px;
  }

  @media screen and (max-width: @medium) {
    display: none;
  }
}

.header__address-list-item {
  color: @white;
  padding-left: 40px;
  position: relative;

  &:first-child::after {
    content: "";
    display: block;
    width: 25px;
    height: 25px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("../img/icons/location-icon.png");
  }

  &:last-child {
    padding-left: 0;
  }

  @media screen and (max-width: @extra-large) {
    font-size: 14px;
    line-height: 22px;
  }

  @media screen and (max-width: @large) {
    font-size: 12px;
  }
}

.header__tel-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 500px;
  margin-bottom: 17px;

  @media screen and (max-width: @extra-large) {
    max-width: 420px;
  }

  @media screen and (max-width: @large) {
    max-width: 370px;
  }

  @media screen and (max-width: @medium) {
    max-width: 80%;
    margin-right: auto;
  }

  @media screen and (max-width: @small) {
    margin-bottom: 12px;
    flex-wrap: wrap;
    max-width: 100%;
  }

  @media screen and (max-width: 475px) {
    max-width: 136px;
  }
}

.header__tel-list-item {
  color: @white;
  padding-left: 40px;
  position: relative;

  @media screen and (max-width: @small) {
    padding-left: 23px;
  }

  &:first-child::after {
    content: "";
    display: block;
    width: 25px;
    height: 25px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("../img/icons/phone-icon.png");

    @media screen and (max-width: @small) {
      width: 18px;
      height: 18px;
      background-size: cover;
      top: 5px;
    }
  }

  &:last-child {
    padding-left: 0;

    @media screen and (max-width: @medium) {
      margin-right: 10%;
    }

    @media screen and (max-width: 475px) {
      position: relative;
      padding-left: 23px;
      margin-right: 0;

      &::after {
        content: "";
        display: block;
        width: 18px;
        height: 18px;
        position: absolute;
        top: 5px;
        left: 0;
        background-image: url("../img/icons/phone-icon.png");
        background-size: cover;
      }
    }
  }
}

.header__tel-list-link {
  color: @white;
  font-size: 22px;
  font-weight: 700;
  text-decoration: none;

  &:hover {
    color: #e6b038;
  }

  &:active {
    opacity: 0.7;
  }

  @media screen and (max-width: @extra-large) {
    font-size: 16px;
    line-height: 22px;
  }

  @media screen and (max-width: @large) {
    font-size: 14px;
  }

  @media screen and (max-width: @medium) {
    font-size: 12px;
  }

  @media screen and (max-width: @small) {
    font-size: 11px;
  }
}

.header__social-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 520px;
  margin-bottom: 17px;

  @media screen and (max-width: @large) {
    max-width: 400px;
  }

  @media screen and (max-width: @medium) {
    max-width: 80%;
    margin-right: auto;
  }

  @media screen and (max-width: @small) {
    max-width: 100%;
    margin-bottom: 12px;
  }

  @media screen and (max-width: 475px) {
    max-width: 28px;
    margin: 0 auto;
    flex-wrap: wrap;
  }
}

.header__social-list-item {
  color: @white;
  padding-left: 40px;
  position: relative;

  @media screen and (max-width: @small) {
    padding-left: 23px;
  }

  @media screen and (max-width: 475px) {
    padding-left: 0;
    margin-bottom: 3px;
  }

  &:first-child::after {
    content: "";
    display: block;
    width: 28px;
    height: 28px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("../img/icons/facebook-icon.png");

    @media screen and (max-width: @small) {
      width: 18px;
      height: 18px;
      background-size: cover;
      top: 5px;
    }

    @media screen and (max-width: 475px) {
      display: none;
    }
  }

  &:last-child::after {
    content: "";
    display: block;
    width: 28px;
    height: 28px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("../img/icons/instagram-icon.png");

    @media screen and (max-width: @small) {
      width: 18px;
      height: 18px;
      background-size: cover;
      top: 5px;
    }

    @media screen and (max-width: 475px) {
      display: none;
    }
  }
}

.header__social-list-link {
  color: @white;
  text-decoration: none;

  &:hover {
    color: #e6b038;
  }

  &:active {
    opacity: 0.7;
  }

  @media screen and (max-width: @extra-large) {
    font-size: 16px;
    line-height: 22px;
  }

  @media screen and (max-width: @large) {
    font-size: 14px;
  }

  @media screen and (max-width: @medium) {
    font-size: 12px;
  }

  @media screen and (max-width: @small) {
    font-size: 10px;
  }

  @media screen and (max-width: 475px) {
    font-size: 0;
  }
}

.header__social-list-link--fb {
  @media screen and (max-width: 475px) {
    display: block;
    width: 28px;
    height: 28px;

    &::after {
      content: "";
      display: block;
      width: 28px;
      height: 28px;
      position: absolute;
      top: 0;
      left: 0;
      background-image: url("../img/icons/facebook-icon.png");
    }
  }
}

.header__social-list-link--inst {
  @media screen and (max-width: 475px) {
    display: block;
    width: 28px;
    height: 28px;

    &::after {
      content: "";
      display: block;
      width: 28px;
      height: 28px;
      position: absolute;
      top: 0;
      left: 0;
      background-image: url("../img/icons/instagram-icon.png");
    }
  }
}

.header__bottom {
  width: 100%;
  padding-left: 147px;

  @media screen and (max-width: @large) {
    padding-left: 10px;
  }
}

.header__nav {
  padding-top: 17px;
  padding-bottom: 17px;
}

.header__nav-list {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media screen and (max-width: @medium) {
    flex-direction: column;
    background-image: url(../img/bg-decor-1.png);
    background-repeat: repeat;
  }
}

.header__nav-list-item {
  @media screen and (max-width: @medium) {
    margin-bottom: 10px;
  }
}

.header__nav-list-link {
  color: @black;
  font-weight: 500;
  position: relative;

  &::after {
    content: "";
    display: none;
    width: 100%;
    height: 2px;
    position: absolute;
    bottom: -20px;
    left: 0;
    background-color: @red;
  }

  &:hover,
  &:active {
    color: @red;
    text-decoration: none;

    &::after {
      display: block;
    }
  }

  @media screen and (max-width: @extra-large) {
    font-size: 12px;
  }

  @media screen and (max-width: @medium) {
    &::before {
      content: "";
      display: block;
      width: 100%;
      height: 2px;
      position: absolute;
      bottom: -12px;
      left: 0;
      background-color: @border-color;
      transition: all ease 0.3s;
    }

    &::after {
      content: "";
      display: none;
      width: 100%;
      height: 2px;
      position: absolute;
      bottom: -12px;
      left: 0;
      background-color: @red;
      z-index: 2;
      transition: all ease 0.3s;
    }

    &:hover {
      &::after {
        display: none;
      }
    }

    &:active {
      color: @red;
      text-decoration: none;

      &::after {
        display: block;
      }
    }
  }
}
