.mobile-btn {
  width: 54px;
  height: 54px;
  outline: none;
  background-color: @red;
  border: 1px solid @white;
  border-right: none;

  &::before {
    content: "";
    display: block;
    width: 44px;
    height: 6px;
    position: absolute;
    top: 8px;
    left: 5px;
    background-color: @white;
    border-radius: 5px;
  }

  &::after {
    content: "";
    display: block;
    width: 44px;
    height: 6px;
    position: absolute;
    bottom: 8px;
    left: 5px;
    background-color: @white;
    border-radius: 5px;
  }
}

.mobile-btn__decor {
  display: block;
  width: 36px;
  height: 6px;
  position: absolute;
  top: 23px;
  left: 12px;
  background-color: @white;
  border-radius: 5px;
}

.mobile-btn__close {
  &::before {
    transform: rotate(45deg);
    top: 22px;
  }

  &::after {
    transform: rotate(-45deg);
    bottom: 24px;
  }

  .mobile-btn__decor {
    display: none;
  }
}
