.exams {
  padding-bottom: 55px;
}

.exams__title {
  width: 100%;
  margin-bottom: 30px;
}

.exams__wrapper {
  width: 100%;
}

.exams__dscr-wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;

  @media screen and (max-width: @medium) {
    flex-direction: column;
  }
}

.exams__img-wrapper {
  width: 100%;
  max-width: 550px;
  margin-right: 30px;
  min-height: 400px;
  background-color: #efefef;
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;

  @media screen and (max-width: @large) {
    margin: 0 auto;
    margin-bottom: 20px;
    max-width: 450px;
    margin-right: 20px;
    min-height: 300px;
  }

  @media screen and (max-width: @medium) {
    box-sizing: border-box;
    padding: 10px;
    width: 90%;
    margin: 0 auto;
    margin-bottom: 20px;
  }
}

.exams__img {
  width: 70%;
  height: auto;
  object-fit: contain;
}

.exams__dscr {
  width: 100%;
  max-width: 550px;

  @media screen and (max-width: @medium) {
    width: 100%;
    max-width: 100%;
  }
}

.exams__dscr-txt {
  @media screen and (max-width: @small) {
    box-sizing: border-box;
    padding: 0 10px;
  }
}

.exams__lists-title {
  font-size: 24px;
  line-height: 34px;
  font-weight: 800;
  margin-bottom: 20px;

  @media screen and (max-width: @small) {
    padding: 0 10px;
  }
}

.exams__lists-wrapper-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media screen and (max-width: @medium) {
    flex-direction: column;
  }
}

.exams__list-wrapper {
  width: 45%;

  @media screen and (max-width: @medium) {
    width: 90%;
  }

  @media screen and (max-width: @small) {
    width: 100%;
    padding: 0 10px;
  }
}

.exams__list-wrapper--left {
  @media screen and (max-width: @medium) {
    margin-bottom: 40px;
  }
}

.exams__list-decor {
  padding: 12px 22px;
  max-width: 265px;
  width: 100%;
  margin-bottom: 12px;
  text-align: center;
  border-radius: 30px;
  color: @white;
  font-weight: bold;
  background-image: linear-gradient(-180deg, rgb(231, 82, 85) 0%, rgb(112, 33, 34) 100%, rgb(29, 5, 23) 100%, rgb(40, 17, 5) 100%);
}

.exams__list-sub-title {
  display: block;
  font-size: 24px;
  line-height: 34px;
  font-weight: 400;
  margin-bottom: 15px;
}

.exams__list-item {
  box-sizing: border-box;
  padding: 12px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &:nth-child(odd) {
    background-color: #f0f0f0;
  }
}

.exams__list-item-colum--txt {
  max-width: 70%;
  width: 100%;
}

.exams__list-item-colum--price {
  text-align: right;
  font-weight: 800;
  width: 30%;
}
