body {
  font-family: "Montserrat", Arial, sans-serif;
  font-size: 16px;
  line-height: 26px;
  font-weight: 400;
  color: @color-text;
  background-color: @white;
}

.big-container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;

  @media screen and (max-width: @laptop) {
    max-width: 1366px;
    box-sizing: border-box;
    padding: 0 20px;
  }

  @media screen and (max-width: 475px) {
    padding: 0 10px;
  }
}

a {
  text-decoration: none;
  transition: all ease .3s;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.vis-hidd {
  position: absolute !important;
	clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
	clip: rect(1px, 1px, 1px, 1px);
	padding:0 !important;
	border:0 !important;
	height: 1px !important;
	width: 1px !important;
	overflow: hidden;
}

main {
  padding-top: 165px;
  background: url(../img/bg-decor-1.png) no-repeat 0 0,
    url(../img/bg-decor-2.png) no-repeat 100% 0;
  background-size: 433px 755px;

  @media screen and (max-width: @medium) {
    padding-top: 125px;
    background: none;
  }

  @media screen and (max-width: 475px) {
    padding-top: 75px;
  }
}
