.map {

}

.map__wrapper {
  position: relative;
  z-index: 1;
}

.map__iframe {
  width: 100%;

  @media screen and (max-width: @small) {
    display: none;
  }
}

.map__container-wrapper {
  margin-top: -608px;
  min-height: 600px;
  position: relative;
  z-index: 10;
  box-sizing: border-box;
  padding-top: 20px;

  @media screen and (max-width: @small) {
    margin-top: 0;
  }
}

.map__form-wrapper {
  background-color: @white;
}
