.programs {
  padding-bottom: 55px;
}

.programs__main-dscr {
  @media screen and (max-width: @small) {
    box-sizing: border-box;
    padding: 0 10px;
  }
}

.programs__article-title,
.programs__list-title {
  font-weight: 800;
  font-size: 24px;
  line-height: 34px;
  margin-bottom: 20px;
}

.programs__article {
  margin-bottom: 40px;

  @media screen and (max-width: @small) {
    box-sizing: border-box;
    padding: 0 10px;
  }
}

.programs__list-block {
  width: 100%;
}

.programs__list-wrapper {
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: @small) {
    flex-direction: column;
    box-sizing: border-box;
    padding: 0 10px;
  }
}

.programs__list-item {
  box-sizing: border-box;
  padding-left: 20px;
  position: relative;
  margin-bottom: 10px;

  &::after {
    content: "";
    display: block;
    width: 9px;
    height: 17px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url(../img/icons/list-arrow-icon.png);
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: contain;
  }
}
