.about-us {
  padding-bottom: 55px;
}

.about-us__title {
  margin-bottom: 20px;
  width: 100%;

  @media screen and (max-width: @large) {
    width: 100%;
  }
}

.about-us__dscr-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  min-height: 400px;

  @media screen and (max-width: @large) {
    flex-direction: column;
    width: 100%;
  }
}

.about-us__img-wrapper {
  width: 100%;
  max-width: 550px;
  margin-right: 30px;
  background-color: #efefef;
  border-radius: 30px;
  text-align: center;
  padding: 30px 0;

  @media screen and (max-width: @large) {
    margin: 0 auto;
    margin-bottom: 20px;
  }

  @media screen and (max-width: @small) {
    box-sizing: border-box;
    padding: 10px;
    width: 90%;
    margin: 0 auto;
    margin-bottom: 20px;
  }
}

.about-us__img {
  width: 70%;
  height: auto;
  object-fit: contain;
}

.about-us__dscr {
  width: 100%;
  max-width: 550px;

  @media screen and (max-width: @large) {
    max-width: 100%;
  }

  @media screen and (max-width: @small) {
    box-sizing: border-box;
    padding: 0 10px;
  }
}

.about-us__dscr-title,
.about-us__dscr-more-title {
  display: block;
  font-size: 24px;
  line-height: 34px;
  margin-bottom: 15px;
}

.about-us__dscr-txt {
  border-bottom: 1px solid #d5d5d5;
  padding-bottom: 20px;

  @media screen and (max-width: @large) {
    width: 100%;
  }
}

.about-us__price {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media screen and (max-width: @large) {
    width: 70%;
    margin: 0 auto;
    margin-bottom: 50px;
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
    width: 100%;
  }
}

.about-us__dscr-more {
  @media screen and (max-width: @small) {
    box-sizing: border-box;
    padding: 0 10px;
  }
}
