.vacancies {
  padding-bottom: 55px;
}

.vacancies__wrapper {
  @media screen and (max-width: @small) {
    padding: 0 10px;
  }
}

.vacancies__title {
  width: 100%;
  margin-bottom: 20px;
}

.vacancies__main-slogan {
  display: block;
  font-weight: 700;
  margin-bottom: 15px;
}

.vacancies__advantages-list {
  padding-left: 20px;
  list-style: outside;
  margin-bottom: 40px;
}

.vacancies__list {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media screen and (max-width: @small) {
    flex-direction: column;
  }
}

.vacancies__list-item {
  border-radius: 30px;
  box-shadow: 9.235px 11.82px 55px 0px rgba(0, 0, 0, 0.1);
  background-color: @white;
  width: 45%;
  box-sizing: border-box;
  padding: 30px;

  @media screen and (max-width: @small) {
    width: 100%;
    margin-bottom: 20px;
  }
}

.vacancies__item-title {
  display: block;
  margin-bottom: 15px;
}

.vacancies__skills-list {
  margin-bottom: 25px;
}

.vacancies__skills-list-item {
  margin-bottom: 10px;
  &::before {
    content: "-";
  }
}
