.section-title {
  width: 100%;
  max-width: 550px;
  line-height: 50px;
  font-size: 30px;
  margin: 0 auto;
  margin-top: -25px;
  margin-bottom: 65px;
  color: @color-text;
  background-color: @white;
  text-align: center;
  position: relative;

  &::before {
    content: "";
    width: 15px;
    height: 15px;
    border: 1px solid @border-color;
    display: block;
    border-radius: 50%;
    position: absolute;
    top: 17px;
    left: 0;
  }

  &::after {
    content: "";
    width: 15px;
    height: 15px;
    border: 1px solid @border-color;
    display: block;
    border-radius: 50%;
    position: absolute;
    top: 17px;
    right: 0;
  }

  @media screen and (max-width: @small) {
    max-width: 320px;
  }
}
