.btn {
  padding-top: 22px;
  padding-bottom: 22px;
  background-color: @blue;
  width: 100%;
  max-width: 350px;
  border-radius: 25px;
  color: @white;
  font-size: 20px;
  font-weight: 500;
  border: none;

  &:hover {
    color: @white;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(36,85,153,0.6);
    -moz-box-shadow: 0px 0px 10px 0px rgba(36,85,153,0.6);
    box-shadow: 0px 0px 10px 0px rgba(36,85,153,0.6);
  }

  &:active {
    opacity: .7;
  }
}
