.breadcrumbs {
  padding-top: 50px;
  padding-bottom: 30px;

  @media screen and (max-width: @extra-large) {
    padding-top: 30px;
  }

  @media screen and (max-width: @large) {
    padding-top: 10px;
  }

  @media screen and (max-width: 475px) {
    padding-top: 50px;
  }
}

.breadcrumbs__list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  max-width: 420px;
  color: #888888;

  @media screen and (max-width: @small) {
    padding-left: 15px;
  }
}

.breadcrumbs__list-item {
  padding-right: 40px;
  position: relative;

  &:first-child::after {
    content: "";
    width: 8px;
    height: 14px;
    display: block;
    position: absolute;
    top: 6px;
    right: 12px;
    background-image: url(../img/icons/breadcrumbs-arrow-icon.png);
    background-size: cover;
    background-position: 50%;
  }

  &:last-child {
    padding-right: 0;
  }

  @media screen and (max-width: @small) {
    font-size: 14px;
  }
}

.breadcrumbs__list-link {
  color: #888888;
  transition: all ease .3s;

  &:hover {
    color: @color-text;
    text-decoration: none;
  }
}
