.slider {
  padding-top: 70px;
  padding-bottom: 65px;
  position: relative;
  z-index: 0;

  @media screen and (max-width: @medium) {
    background: none;
  }
}

.slider__container-wrapper {
  width: 100%;
}

.slider__item {
  min-height: 550px;
  width: 100%;
  border-radius: 25px;
  background-image: url(../img/preview.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
  box-sizing: border-box;
  padding-top: 100px;
  padding-bottom: 60px;
  padding-left: 80px;
  padding-right: 80px;

  @media screen and (max-width: @small) {
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 20px;
    padding-top: 20px;
    min-height: 250px;
  }
}

.slider__info {
  width: 100%;
  max-width: 600px;
}

.slider__title {
  display: block;
  font-size: 50px;
  line-height: 64px;
  margin-bottom: 20px;
  min-height: 128px;

  @media screen and (max-width: @medium) {
    font-size: 32px;
    line-height: 46px;
  }

  @media screen and (max-width: @small) {
    font-size: 28px;
    line-height: 42px;
  }
}

.slider__txt {
  display: block;
  font-size: 24px;
  line-height: 32px;
  margin-bottom: 30px;
  min-height: 64px;

  @media screen and (max-width: @small) {
    font-size: 14px;
    line-height: 28px;
  }
}

.slick-arrow {
  position: absolute;
  top: 50%;
  z-index: 1;
  font-size: 0;
}

.slick-next {
  left: 20px;
  width: 25px;
  height: 37px;
  background-image: url(../img/icons/left-arrow-icon.png);
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: contain;
  background-color: transparent;
  border: none;

  &:hover {
    opacity: 0.8;
  }
}

.slick-prev {
  right: 20px;
  width: 25px;
  height: 37px;
  background-image: url(../img/icons/right-arrow-icon.png);
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: contain;
  background-color: transparent;
  border: none;

  &:hover {
    opacity: 0.8;
  }
}

.slick-list {
  @media screen and (max-width: @medium) {
    margin-left: -10px;
  }
}

.slick-slide {
  @media screen and (max-width: @medium) {
    margin-left: 20px;
  }
}
