.test {
  padding-bottom: 70px;
}

.test__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  border-radius: 25px;
  background: url(../img/pattern.png) no-repeat 50%,
    url(../img/test-bg.png) no-repeat 50%/cover;
  min-height: 150px;
  position: relative;
  box-sizing: border-box;
  padding-left: 170px;
  padding-right: 50px;

  box-shadow: 9.235px 11.82px 55px 0 rgba(0, 0, 0, 0.1);

  &::after {
    content: "";
    width: 153px;
    height: 164px;
    display: block;
    position: absolute;
    top: -25px;
    left: 15px;
    background-image: url(../img/test-img.png);
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
  }

  @media screen and (max-width: @extra-large) {
    flex-wrap: wrap;
    padding-top: 20px;
    padding-bottom: 20px;

    &::after {
      content: "";
      width: 173px;
      height: 184px;
      display: block;
      position: absolute;
      top: -25px;
      left: 5px;
      background-image: url(../img/test-img.png);
      background-size: cover;
      background-position: 50%;
      background-repeat: no-repeat;
    }
  }

  @media screen and (max-width: 485px) {
    padding: 20px;
    width: 90%;
    margin: 0 auto;

    &::after {
      content: "";
      width: 63px;
      height: 74px;
      display: block;
      position: absolute;
      top: -35px;
      left: 0;
      background-image: url(../img/test-img.png);
      background-size: cover;
      background-position: 50%;
      background-repeat: no-repeat;
      z-index: 0;
    }
  }
}

.test__txt {
  color: @white;
  font-size: 24px;
  line-height: 34px;
  font-weight: 700;
  width: 100%;
  max-width: 460px;

  span {
    font-weight: 400;
  }

  @media screen and (max-width: @extra-large) {
    font-size: 20px;

    span {
      display: block;
    }
  }

  @media screen and (max-width: @large) {
    max-width: 100%;
    text-align: center;
  }

  @media screen and (max-width: 485px) {
    padding-left: 20px;
    position: relative;
    z-index: 2;
  }
}

.test__link {
  @media screen and (max-width: @extra-large) {
    margin-left: auto;
  }

  @media screen and (max-width: @large) {
    margin-right: auto;
  }
}
