.language {
  padding-bottom: 55px;
}

.language__title {
  margin-bottom: 20px;

  @media screen and (max-width: @large) {
    width: 100%;
  }
}

.language__dscr-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  min-height: 400px;

  @media screen and (max-width: @large) {
    flex-direction: column;
    width: 100%;
  }
}

.language__img-wrapper {
  width: 100%;
  max-width: 550px;
  margin-right: 30px;

  @media screen and (max-width: @large) {
    margin: 0 auto;
    margin-bottom: 20px;
  }

  @media screen and (max-width: @small) {
    box-sizing: border-box;
    padding: 0 10px;
  }
}

.language__img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.language__dscr {
  width: 100%;
  max-width: 550px;

  @media screen and (max-width: @large) {
    max-width: 100%;
  }

  @media screen and (max-width: @small) {
    box-sizing: border-box;
    padding: 0 10px;
  }
}

.language__dscr-title,
.language__dscr-more-title {
  display: block;
  font-size: 24px;
  line-height: 34px;
  margin-bottom: 15px;
}

.language__dscr-txt {
  border-bottom: 1px solid #d5d5d5;
  padding-bottom: 20px;

  @media screen and (max-width: @large) {
    width: 100%;
  }
}

.language__price {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media screen and (max-width: @large) {
    width: 70%;
    margin: 0 auto;
    margin-bottom: 50px;
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
    width: 100%;
  }
}

.language__price-txt {
  font-weight: 800;
  margin-bottom: 0;

  @media screen and (max-width: @small) {
    margin-bottom: 20px;
  }
}

.language__price-mini-title {
  display: block;
  margin-bottom: 10px;
}

.language__price-red-txt {
  font-size: 34px;
  line-height: 36px;
  color: @red;
}

.language__price-currency {
  color: @red;
}

.language__dscr-more {
  @media screen and (max-width: @small) {
    box-sizing: border-box;
    padding: 0 10px;
  }
}
