.footer {
  padding-top: 15px;
  padding-bottom: 15px;
  background-color: @dark-grey;
}

.footer__wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  @media screen and (max-width: @medium) {
    flex-direction: column;
  }

  @media screen and (max-width: @medium) {
    box-sizing: border-box;
    padding: 0 6px;
  }
}

.footer__logo {
  margin-top: 20px;
  margin-bottom: 20px;
}

.footer__center-wrapper {
  width: 100%;
  max-width: 500px;
}

.footer__address-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 480px;
  margin-bottom: 17px;

  @media all and (max-width: @laptop) {
    margin-right: 40px;
  }

  @media all and (max-width: @small) {
    flex-direction: column;
  }

  @media all and (max-width: @small) {
    max-width: 320px;
    margin: 0 auto;
    margin-bottom: 20px;
  }
}

.footer__address-list-item {
  color: @white;
  padding-left: 40px;
  position: relative;

  &:first-child::after {
    content: "";
    display: block;
    width: 25px;
    height: 25px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("../img/icons/location-icon.png");
  }

  &:last-child {
    padding-left: 0;
  }
}

.footer__tel-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 500px;
  margin-bottom: 17px;

  @media all and (max-width: @small) {
    max-width: 320px;
    margin: 0 auto;
    margin-bottom: 20px;
    flex-direction: column;
    align-items: flex-start;
  }
}

.footer__tel-list-item {
  color: @white;
  padding-left: 40px;
  position: relative;

  &:first-child::after {
    content: "";
    display: block;
    width: 25px;
    height: 25px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("../img/icons/phone-icon.png");
  }

  &:last-child {
    padding-left: 0;
    @media all and (max-width: @small) {
      padding-left: 40px;
    }
  }
}

.footer__tel-list-link {
  color: @white;
  font-size: 22px;
  font-weight: 700;
  text-decoration: none;

  &:hover {
    color: #e6b038;
    text-decoration: none;
  }

  &:active {
    opacity: 0.7;
  }
}

.footer__social-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 480px;
  margin-bottom: 17px;

  @media screen and (max-width: @extra-large) {
    flex-direction: column;
    max-width: 320px;
  }

  @media screen and (max-width: @large) {
    flex-direction: row;
    max-width: 480px;
    margin-left: auto;
  }

  @media screen and (max-width: @medium) {
    margin-left: 0;
    max-width: 500px;
  }

  @media all and (max-width: @small) {
    max-width: 320px;
    margin: 0 auto;
    margin-bottom: 20px;
    flex-direction: column;
    align-items: flex-start;
  }
}

.footer__social-list-item {
  color: @white;
  padding-left: 40px;
  position: relative;
  margin-bottom: 20px;

  &:first-child::after {
    content: "";
    display: block;
    width: 28px;
    height: 28px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("../img/icons/facebook-icon.png");
  }

  &:last-child::after {
    content: "";
    display: block;
    width: 28px;
    height: 28px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("../img/icons/instagram-icon.png");
  }
}

.footer__social-list-link {
  color: @white;
  text-decoration: none;

  &:hover {
    color: #e6b038;
    text-decoration: none;
  }

  &:active {
    opacity: 0.7;
  }
}
