.our-schools {
  border-top: 1px solid @border-color;
  padding-bottom: 70px;
}

.our-schools__img-wrapper {

}

.our-school-address {
  width: 100%;
  margin-top: 20px;

  tr td:first-child {
    font-weight: 800;

    @media screen and (max-width: @extra-large) {
      font-size: 14px;
    }
  }

  tr td:last-child {
    padding-left: 10px;

    @media screen and (max-width: @extra-large) {
      font-size: 12px;
    }
  }
}

.our-schools_img {
  width: 100%;
  height: auto;
}
