.lang-sel {
  border-top: 1px solid @border-color;
  padding-bottom: 70px;
}

.lang-sel__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  @media screen and (max-width: @laptop) {
    justify-content: flex-start;
    max-width: 1296px;
    margin: 0 auto;
  }
}

.lang-sel__list-item {
  width: 376px;
  height: 380px;
  margin-bottom: 5px;
  position: relative;
  border-radius: 10px;
  background-image: url(../img/uk-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
  background-color: @grey;

  @media screen and (max-width: @laptop) {
    width: 300px;
    height: 300px;
    margin: 0 12px;
    margin-bottom: 5px;
  }

  @media screen and (max-width: @extra-large) {
    width: 210px;
    height: 262px;
    margin: 0 10px;
    margin-bottom: 5px;
  }

  @media screen and (max-width: @medium) {
    width: 300px;
    height: 300px;
    margin-left: auto;
    margin-right: auto;
  }
}

.lang-sel__list-link {
  display: block;
  width: 202px;
  height: 202px;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -101px;
  margin-left: -101px;
  transition: all ease 0.3s;

  @media screen and (max-width: @laptop) {
    width: 162px;
    height: 162px;

    margin-top: -81px;
    margin-left: -81px;
  }

  &:hover {
    transform: scale(1.05);
    text-decoration: none;
  }
}

.lang-sel__list-img {
  @media screen and (max-width: @laptop) {
    width: 162px;
    height: 162px;
  }
}

.leng-sel__title {
  color: @white;
  font-size: 24px;
  font-weight: bold;
  margin-top: 10px;
  display: block;
}

